export default {
	name: "w-loading",
	props: {
		text: String,
		mask: Boolean | String,
		click: Boolean | String,
	},
	data() {
		return {
			show: false
		};
	},
	methods: {
		preventTouchMove() {
			console.log('stop user scroll it!');
			return;
		},
		Mclose() {
			if (this.click == 'false' || this.click == false) {
				this.show = false
			}
		},
		open() {
			this.show = true
		},
		close() {
			this.show = false
		}
	}
};
